<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <h2>Duplicate Positions In The Same Organization Structure And Same Facility</h2>
    <v-expansion-panels>
      <v-expansion-panel
        v-for="(dupp,i) in dupplicates"
        :key="i"
      >
        <v-expansion-panel-header>
          {{dupp.name}} - {{ dupp.dupps.length }} Dupplicates
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row>
            <v-col cols="6">
              <v-btn @click="openNewTab(dupp.pos)">Open</v-btn>
              <fhir-page-view :id="dupp.pos" page="position" :showDrawer="false" @templates-loaded="rows[dupp.dupps[0]] = true" />
            </v-col>
            <v-col v-for="(duppof, index) in dupp.dupps" :key="index" cols="6">
              <v-btn v-if="rows[duppof]" @click="openNewTab(duppof)">Open</v-btn>
              <fhir-page-view v-if="rows[duppof]" :id="duppof" page="position" :showDrawer="false" @templates-loaded="rows[dupp.dupps[index+1]] = true"/>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <div v-if="!overlayUnclosed && !dupplicates.length">
      <b>No dupplicates</b>
    </div>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      valid: true,
      overlayUnclosed: false,
      displaySecond: false,
      dupplicates: [],
      rows: {}
    }
  },
  components: {
    "fhir-page-view": () => import("@/views/fhir-page-view")
  },
  methods: {
    openNewTab(id) {
      window.open('/resource/view/position/' + id, '_blank');
    },
    templatesLoaded(dupps, index) {
      if(dupps) {
        this.displaySecond = true
        this.rows[dupps.dupps[0]] = true
      } else {
        this.displaySecond = false
        this.rows[index] = true
      }
    },
    getDupplicates() {
      this.overlayUnclosed = true
      fetch("/site/practitioner/duppPositionsByStruct").then(response => response.json().then((response) => {
        this.overlayUnclosed = false
        for(let dupp1 of response) {
          for(let dupp2 of dupp1.dupps) {
            this.$set(this.rows, dupp2, false)
          }
        }
        this.dupplicates = response
      }).catch(() => {
        this.overlayUnclosed = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
        // return this.$router.go(-1)
      })).catch(() => {
        this.overlayUnclosed = false
        this.$store.commit("setMessage", {
          type: "error",
          text: "An error has occured, try later",
        });
        // return this.$router.go(-1)
      })
    }
  },
  created() {
    this.getDupplicates()
  }
}
</script>